<template>

    <div id="Note" class="container">
        <transition name="fade">
            <div v-if="!editDossier && !editAttachments && !editTags">
                <div class="row">
                    <button v-on:click='$emit("back_notes")'>
                        <i class="fas fa-caret-left back"></i>
                    </button>
                </div>
                
        
                <div class="container">
        
                    <div class="row">
                        
                        <div class="right2 darkcyan"><b>{{this.time}}</b></div>
                        
        
                        <div class="left2 darkcyan"><b> {{this.date}}</b> </div>   
                        
                    </div>
                    <div v-if="!edit" class="row darkcyan background_darkcyan top1"><b>{{this.title}}</b></div>
                    <input id="note_inputTitle" v-on:keyup='editTitle' v-if="edit" type="text" class="row top1" v-bind:value='this.title' >
        
                    <div id="note_divBody" v-if="!edit" class="row black top1">{{this.body}}             
                    </div>
                    <textarea  class="row top1" id='note_textareaBody' v-if="edit" maxlength="6000"
                                v-bind:value='this.body' v-on:keyup='editBody'
                    >                
                    </textarea>
                    
                    <div class="row top1">
                        <button v-if='this.attachments!=""' class="col-2" v-on:click='editAttachmentsOn'><i  class="fas fa-paperclip"> {{this.attachments.length}} </i></button>
                        <button v-if='this.attachments=="" && this.noAttachments' class="col-2" v-on:click='editAttachmentsOn'><i  class="fas fa-paperclip"> 0 </i></button>
                        <div class="col-2" v-if='this.attachments=="" && !this.noAttachments'><img style="height:40px;width:auto;" class="col" src="../assets/loading.gif" alt="loading"> </div>

                        <button v-if="this.dossie!=''" class="col" v-on:click='editDossierOn' ><i  class="fas fa-folder"> {{this.dossier.dossier_name}}</i>  </button>                
                        <div class="col-2" v-if='this.dossier==""'><img style="height:40px;width:auto;" class="col" src="../assets/loading.gif" alt="loading"> </div>
                        <button class="col-2" v-on:click='editTagsOn'><i class="fas fa-tag"> {{this.tags.length}}</i></button>
                    </div>

                </div>

                <div v-if="!edit" class="row top2 note_actions_container">
                    
                    <button class="note_actions" v-on:click='pdf'><i class="fas fa-file-pdf black"></i></button>                  
                    <button class="left2 note_actions" v-on:click='editOn'><i class="far fa-edit black"></i></button>
                </div>    

                <div v-if="edit" class="row top1 note_actions_container">
                        <div class="col-10"></div>
                        <button v-on:click='editOff' class="black">Done</button>                              
                </div>    


            </div>
        </transition>

        <transition name="fade">        
            <EditDossier
                v-if="editDossier"
                v-bind:dossier='dossier'
                v-on:back_noteDossier='back_noteDossier'
             />
        </transition>

        <transition name="fade">
            <EditAttachments                    
                    v-if="editAttachments"
                    v-on:back_noteAttachments='back_noteAttachments'
                    v-bind:attachments='this.attachments'
                    v-bind:noAttachments='this.noAttachments'
                    v-bind:noteData='this.noteData'
                    v-on:getAttachments='getAttachments'


                
            />
        </transition>

        

        <transition name="fade">
            <EditTags
                    v-if="editTags"                
                    v-on:back_noteTags='back_noteTags'
                    v-on:getTags='getTags'
                    v-bind:tags="this.tags"
                    v-bind:noteData='this.noteData'

                 />
        </transition>

        <!-- <transition name="down">
            <ViewImage
                    v-if="viewImage"                
                    v-on:back_noteImage='back_noteImage'                    
                    v-bind:noteData='this.noteData'

                 />
        </transition> -->







        


    </div>    
    
</template>


<script>

import EditDossier from './EditDossier';
import EditAttachments from './EditAttachments';
import EditTags from './EditTags';



import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";



pdfMake.vfs = pdfFonts.pdfMake.vfs;


export default {
    name:'Note',

    components:{
        EditDossier,EditAttachments,EditTags
    },

    data:function(){
        return{
           date:(()=>{

               let months=['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];

               let fullDate=this.noteData[0].date;
                   fullDate=fullDate.split('-');
               
               let year=fullDate[0];
               let month=fullDate[1];
                   months.forEach((m,index)=>{
                       if(month==index+1 || month== '0'+index+1){
                           month=m;
                       } 
                   });
               let day=fullDate[2];
                   day=day.substring(0,2);

               let newDate=day+' '+ month+' '+year;
                return newDate;
           })(),

           time:(()=>{
                
                let fullTime=this.noteData[0].time;
                    fullTime=fullTime.split(':');
                let hours=fullTime[0];
                let minutes=fullTime[1];
                let newTime=hours+':'+minutes;
                return newTime;
           })(),

           title:(()=>{
                return this.noteData[0].title;    
           })(),

           body:(()=>{
                return this.noteData[0].body;    
           })(),

           attachments:[],
           tags:[],
           dossier:'',
           noAttachments:false,
           edit:false,
           editAttachments:false,
           editDossier:false,
           editTags:false,
           

        }
    },

    props:['noteData'],

    mounted:function(){    

        this.getDossier();
        this.getAttachments();
        this.getTags();
        

    },

    watch:{
         logo:function(newVal){
               this.logo=newVal;  
         }, 
        //  dossier:function(newVal){
        //        this.dossier=newVal;   
               
        //  },
        //  tags:function(newVal){
        //          this.new_tags=newVal;                 
        //  },
        
    },

    methods:{
        
        //retrieve note attachments
        getAttachments:function(){   
            this.attachments=[];
            let self=this;         
            let request= new XMLHttpRequest();            
            request.open('GET','https://scripta-app.herokuapp.com/api/note-attachments'); 
    
            let session_id=localStorage.getItem('session_id');
            let note_fk=this.noteData[0].note_id;

            request.setRequestHeader('session_id', session_id);               
            request.setRequestHeader('note_fk', note_fk);               

            //send request
            request.send();  

            request.onload=function(){
                if(this.status==200){

                    
                    let response=JSON.parse(this.responseText);
                    if(response==''){
                        self.noAttachments=true;
                    }
                    
                    response.forEach(r => {
                    self.attachments.push(r);               
                    });

                } 
                else{
                alert(this.responseText);
                }  
            }  


        },


        //retrieve dossier name
        getDossier:function(){
             
            let self=this;
            let request= new XMLHttpRequest();            
            request.open('GET','https://scripta-app.herokuapp.com/api/note-dossier');        

            let session_id=localStorage.getItem('session_id');
            let dossier_fk=this.noteData[0].dossier_fk;

            request.setRequestHeader('session_id', session_id);               
            request.setRequestHeader('dossier_fk', dossier_fk);               

            // send request
            request.send();  

            request.onload=function(){
                if(this.status==200){
                    let response=JSON.parse(this.responseText);                    
                    self.dossier={dossier_fk:response.userDossier_id,dossier_name:response.dossier_name};                                 
                } 
                else{
                    alert(this.responseText); 
                }  
            } 
        },

        //retrieve tags associated with this note
        getTags:function(){

            this.tags=[];
            let self=this;
            
            let request= new XMLHttpRequest();            
            request.open('GET','https://scripta-app.herokuapp.com/api/note-tags');        

            let session_id=localStorage.getItem('session_id');
            let note_fk=this.noteData[0].note_id;

            request.setRequestHeader('session_id', session_id);               
            request.setRequestHeader('note_fk', note_fk);               

            // send request
            request.send();  

            request.onload=function(){
                if(this.status==200){
                    let response=JSON.parse(this.responseText);  
                    response.forEach(r=>{
                        self.tags.push({notesTags_id:r.notesTags_id,note_fk:r.note_fk,tag_fk:r.tag_fk});
                    })                  
                    
                } 
                else{
                    alert(this.responseText); 
                }  
            } 

        },
                
        editOn:function(){
            this.edit=true;
        },
        editOff:function(){
            this.edit=false;
            let note_id=this.noteData[0].note_id;
            let title=this.title;
            let body=this.body;
            
            //send request to update the note with any changes made
            let session_id=localStorage.getItem('session_id');
            

            let request=new XMLHttpRequest();
                            
            request.open('PUT','https://scripta-app.herokuapp.com/api/note');

            request.setRequestHeader('session_id', session_id);

            let data=JSON.stringify({note_id:note_id,title:title,body:body});
            request.send(data);                  

            request.onload = function (){        
                                                                    
                if(this.status==200){ //request is valid
                    
                    // alert(this.responseText);
                
                }
                else{
                    alert(this.responseText);
                }                    
            };



        },

        editTitle:function(){
            let title=document.getElementById('note_inputTitle').value.trim();
            this.title=title;
        },

        editBody:function(){
            let body=document.getElementById('note_textareaBody').value.trim();
            this.body=body;
        },

        editDossierOn:function(){
            
            this.editDossier=true;

        },

        editAttachmentsOn:function(){
            this.editAttachments=true;
        },
        editTagsOn:function(){
            this.editTags=true;
        },

        back_noteDossier:function(currentDossier){
            this.editDossier=false; 
            
                if(currentDossier==''){
                currentDossier='Untitled';                                
                }                    
                this.modifyDossier(currentDossier);
                this.getDossier();
                
                
        },
        back_noteAttachments:function(){
            this.editAttachments=false;
            this.attachments=[];
            this.getAttachments();
            
        },

        back_noteTags:function(){
            this.editTags=false;
        },

        modifyDossier:function(dossier_name){
            let self=this;
            let note_id=this.noteData[0].note_id;
            let session_id=localStorage.getItem('session_id');
            let user_id=localStorage.getItem('user_id');


            //send request to modify dossier                        
            let request=new XMLHttpRequest();
                
            request.open('PUT','https://scripta-app.herokuapp.com/api/assign-new-dossier');

            request.setRequestHeader('session_id', session_id);            
            let data=JSON.stringify({dossier_name:dossier_name ,note_id:note_id,user_fk:user_id});
            request.send(data);                  

            request.onload = function (){        

                if(this.status==200){ //request is valid
                    let response = JSON.parse(this.responseText);
                    console.log(this.responseText);
                    
                    self.dossier={dossier_fk:response.dossier_fk,dossier_name:response.dossier_name};
                }
               else{
                   console.log(this.responseText);
               }                    
            };
        },

        pdf: function(){

        var docDefinition = { content: [

                            {                            
                            image:'data:image/png;base64,',
                            width: 80
                            },
                            {text:'Dossier',
                            color:'darkgray',
                            fontSize:9,
                            marginTop:15
                            },
                            {text:this.dossier.dossier_name,
                            color:'black',
                            fontSize:10,
                            bold:true,
                            marginTop:1
                            },
                            {text:this.title,
                            color:'darkcyan',
                            fontSize:15,
                            marginTop:10
                            },
                            {text:' ',
                            color:'darkcyan',
                            fontSize:5
                            },

                            {text:this.date+ ' - '+this.time,
                            color:'gray',
                            fontSize:10
                            },
                            {text:' ',
                            color:'darkcyan',
                            fontSize:5
                            },
                            
                            {text:this.body,
                            color:'black',
                            fontSize:9
                            
                            
                            
                            }
                            
                            ] };
        // pass the "win" argument
        pdfMake.createPdf(docDefinition).download();

        },
        

       //

    },
}
</script>


<style>


    #note_divBody{
        position: relative;
        text-align: justify;
        white-space: pre-wrap;
        height: 200px;
        overflow-y: scroll;
        padding: 1%;
        /* background-color: rgb(0, 139, 139,0.05); */
        /* border: solid rgba(0,0,0,0.1) thin; */
        box-shadow: 0px 0px 3px rgba(0,0,0,0.6);
        border-radius: 5px;

    }
    
   #note_textareaBody {
        
        resize: none;
        text-align: justify;
        width: 100%;                
        height: 200px;
        border: solid rgba(0,0,0,0.1) thin;
        color: rgba(0,0,0,0.8);
        
        /* overflow-y: scroll; */
        padding: 1%;
    }
 .note_actions i {font-size: 1.5em;}
 
 .note_actions_container {
        
        
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 100%;
        
    }
    

</style>

